.slider {
    position: relative;
    width: 100%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    overflow: hidden;
}

.wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

.sliderImage {
    position: absolute;
    width: 100%;
    height: 100%;
    transition: all 0.5s;
}

.sliderNextImage {
    transform: translateX(100%);
}

.sliderPrevImage {
    transform: translateX(-100%);
}

.image1 {
    width: 100%;
    height: 100%;
    background-image: url(./../../images/slider1.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    box-shadow: inset 0px 0px 300px 100px rgba(0, 0, 0, 0.5);
}

