.header {
    font-size: 40px;
    line-height: 50px;
    text-transform: uppercase;
    text-shadow: 0px 0px 10px black, 2px 2px 10px rgba(0, 0, 0, 0.5);
}

.button {
    color: white;
    background: #0066cb;
    font-size: 20px;
    font-weight: 300;
    width: max-content;
    padding: 20px 50px;
    border-radius: 5px;
    margin-top: 40px;
    transition: 0.5s ease;
    cursor: pointer;
}

.button:hover {
    box-shadow: 0px 0px 30px 20px rgba(255, 255, 255, 0.5);
}